.nav-tabs {
  display: flex;
  /*justify-content: space-between;*/
  align-items: center;
  margin-bottom: 1rem;
}

.nav-tabs .nav-link {
  border: none;
  border-bottom: 2px solid transparent;
  text-decoration: none;
  padding: 0.5rem 1rem;
  margin-right: 0.5rem;
  transition: border-color 0.2s ease-in-out;
}

.nav-tabs .nav-link.active {
  border-color: #007bff;
}

